@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "Roboto", sans-serif; }
  body .app {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    display: flex; }
    body .app .stage {
      flex: 1 1;
      height: 100%; }
      body .app .stage .grid-wrapper {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center; }
        body .app .stage .grid-wrapper .grid {
          display: flex;
          flex-wrap: wrap; }
          body .app .stage .grid-wrapper .grid .node-border {
            border: 1px solid #1abc9c;
            border-radius: 15%; }
          body .app .stage .grid-wrapper .grid .node {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 15%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain; }
            body .app .stage .grid-wrapper .grid .node.animate {
              -webkit-animation: node 200ms ease-in-out;
                      animation: node 200ms ease-in-out; }

@-webkit-keyframes node {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes node {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }
    body .app .sidebar {
      width: 250px;
      height: 100%; }
    body .app .sidebar .settings, body .app .expansion-panel .settings {
      width: 100%;
      height: 100%;
      margin-top: 10px; }
      body .app .sidebar .settings .input-wrapper, body .app .expansion-panel .settings .input-wrapper {
        padding: 5px 15px; }
        body .app .sidebar .settings .input-wrapper .input, body .app .expansion-panel .settings .input-wrapper .input {
          width: 100%; }
        body .app .sidebar .settings .input-wrapper .slider-wrapper, body .app .expansion-panel .settings .input-wrapper .slider-wrapper {
          margin-top: 10px; }

